"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle, CardDescription, CardFooter } from "@/components/ui/card";
import { UploadSolutionModal } from "@/components/modals/upload-solution-modal";
import { Download, Edit, Eye, FileText, FileUp, Clock, Tag } from "lucide-react";

interface Exercise {
  id: string;
  title: string;
  subject: string;
  dueDate: string;
  submissionsCount: number;
  hasSolution: boolean;
}

interface ExerciseRequest {
  id: string;
  title: string;
  subject: string;
  description: string;
  price: number;
  status: string;
  submittedDate: string;
  student: { id: string; name: string };
}

const mockExercises: Exercise[] = [
  {
    id: "1",
    title: "Calculus Problem Set",
    subject: "Mathematics",
    dueDate: "2024-03-20",
    submissionsCount: 5,
    hasSolution: true,
  },
  {
    id: "2",
    title: "Physics Practice Problems",
    subject: "Physics",
    dueDate: "2024-03-22",
    submissionsCount: 3,
    hasSolution: false,
  },
];

const mockExerciseRequests: ExerciseRequest[] = [
  {
    id: "exr5", 
    title: "Python Data Structure Task",
    subject: "Computer Science",
    description: "Implement a linked list with insert and delete methods.",
    price: 12.00,
    status: "pending",
    submittedDate: "2024-03-22",
    student: { id: "s2", name: "Bob Williams" },
  },
];

export default function TutorExercisesPage() {
  const [isUploadSolutionModalOpen, setIsUploadSolutionModalOpen] = useState(false);
  const [selectedExercise, setSelectedExercise] = useState<Exercise | null>(null);

  const handleUploadSolution = (exercise: Exercise) => {
    setSelectedExercise(exercise);
    setIsUploadSolutionModalOpen(true);
  };

  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-2xl font-bold">Exercises</h1>
        <p className="text-muted-foreground">
          Review student submissions and manage exercise solutions
        </p>
      </div>

      <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3"> 
        {mockExerciseRequests.length > 0 ? (
           mockExerciseRequests.filter(req => req.status === 'pending').map((request) => ( 
             <Card key={request.id} className="flex flex-col h-full hover:shadow-md transition-shadow">
                <CardHeader className="pb-3">
                    <CardTitle className="text-lg">{request.title}</CardTitle>
                     <CardDescription>
                        <span className="font-medium">{request.subject}</span>
                        <span className="mx-1.5">•</span>
                        <span>From: {request.student.name}</span>
                     </CardDescription>
                </CardHeader>
                <CardContent className="flex-1 space-y-3">
                   {request.description && (
                     <p className="text-sm text-muted-foreground line-clamp-3">{request.description}</p>
                   )}
                   <div className="text-sm text-muted-foreground flex items-center justify-between border-t pt-3">
                       <span className="flex items-center gap-1">
                         <Clock className="h-4 w-4" /> Submitted: {request.submittedDate}
                       </span>
                       <span className="flex items-center gap-1 font-semibold text-green-600 dark:text-green-500">
                         <Tag className="h-4 w-4"/> Payout: €{request.price.toFixed(2)}
                       </span>
                   </div>
                </CardContent>
                 <CardFooter className="border-t pt-3 pb-3">
                    <Button
                      className="w-full"
                      onClick={() => handleUploadSolution(mockExercises[0])}
                    >
                       <FileUp className="mr-2 h-4 w-4" /> Upload Solution
                    </Button>
                 </CardFooter>
             </Card>
           ))
        ) : (
           <p className="text-center text-muted-foreground py-8 md:col-span-2 lg:col-span-3">
               No pending exercise requests found.
           </p>
        )}
      </div>

      <UploadSolutionModal
        isOpen={isUploadSolutionModalOpen}
        onClose={() => {
          setIsUploadSolutionModalOpen(false);
          setSelectedExercise(null);
        }}
        exercise={selectedExercise}
      />
    </div>
  );
} 